.footer {
    background: #201e1c;
    font-size: 15px;
}

footer .socials i {
    padding: 10px 20px 0 0;
    font-size: 40px
}

footer .socials i:hover {
    color: var(--three)
}

.credits h5 {
    margin: 20px 0;
    color: #fff;
    font-family: 'Roboto Condensed', sans-serif;
}
.credits li {
    list-style-type: none;
    color: #8d8d8d;
    font-family: 'Comfortaa', cursive;
    font-size: 15px;
}
.credits li:hover {
    color: var(--one);
}
.credits a {
    text-decoration: none;
}

.copyright {
    background-color: #0e0f0f;
    color: #8d8d8d;
    text-align: center;
    padding: 25px 0 15px 0;
    font-family: 'Comfortaa', cursive;
    font-size: small
}

.fab {
    color: #ffffff;
}

.footer-wave{
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}

@media only screen and (max-width: 800px) {
    .footer {
        padding: 0 30px;
        text-align: center
    }
    .footer a {
        margin-top: 30px
    }
    .credits {
        margin: 30px auto 0px auto
    }
    .credits ul {
        padding: 0!important
    }
  }
  @media only screen and (min-width: 767px) {
    .footer .credits {
        text-align: right
    }

}

.modalWindow::-webkit-scrollbar {
    width: 15px;
}

.modalWindow::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
    -webkit-box-shadow: inset 0 0 6px rgba(231, 47, 1, 0.651);
}

.modalWindow::-webkit-scrollbar-thumb {
    background-color: var(--one);
    outline: 1px solid var(--one);
}

.closeButton {
  text-align: right;
}

.modalWindow {
  transition: 0.8s ease-in;

}

.cFooterlink:hover {
    cursor: pointer;
}

.contribCard {
    border-radius: 5px 5px 10px 10px;
    border: 1px solid rgba(80, 80, 80, 0.322);
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.664);
}

.contribCard:hover {
    box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.22);
}

.contribCard>p {
    color: rgba(97, 97, 97, 0.589);
    font-size: 12px;
    margin-top: 0px
}

.contribCard h5 {
    color: var(--three);
    text-transform: uppercase;
    margin: 20px 0 10px 0
}

.contribCard img {
    border-radius: 5px 5px 0 0;
    width: 100%;
}

.modal-header h5 {
    margin: 0px;
    color: var(--three)
}

.modal-body {
    background: url(/coding_challenge-13/static/media/parallax.4cbbd78c.svg);
    margin: 0;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
